import React from 'react';
import { useEffect, useState } from "react";
import Nav from './components/nav/Nav';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './Home';
// import Roadmap from './Roadmap';
import HomePage from './components/homepage/homepage';
import Footer from './components/footer/Footer';
import Slide from './components/slide/Slide';
import Utility from './Utility';
import { PDFReader } from 'reactjs-pdf-reader';
import Raffle from './Raffle';
import { Provider } from 'react-redux';
import store from "./redux/store";
import Mint from "./Mint";
import Web3 from "web3";

export default function App() {
    const [metamaskConnected, setMetamaskConnnected] = useState(false);
    const [account, setAccount] = useState();
    const [networkId, setNetworkId] = useState();

    useEffect(async () => {
        // await loadWeb3();
        await loadBlockchainData();
    }, []);

    const loadWeb3 = async () => {
        if (window.ethereum) {
            window.web3 = new Web3(window.ethereum);
        } else if (window.web3) {
            window.web3 = new Web3(window.web3.currentProvider);
        } else {
            window.alert(
                "Non-Ethereum browser detected. You should install Metamask wallet first!"
            );
        }
    };

    const loadBlockchainData = async () => {
        if (window.web3) {

            const web3 = window.web3;
            const accounts = await web3.eth.getAccounts();
            const networkId = await web3.eth.net.getId();
            setNetworkId(networkId);

            window.ethereum.on("accountsChanged", (accounts) => {
                if (accounts.length > 0) setAccount(accounts[0]);
                else setAccount();
            });
            window.ethereum.on("networkChanged", (networkId) => {
                setNetworkId(networkId);
            });

            if (accounts.length == 0) {
                setMetamaskConnnected(false);
            } else {
                setMetamaskConnnected(true);
                setAccount(accounts[0]);
            }
        }
    };

    return (
        <Provider store={store}>
            <Router>
                <Nav
                    metamaskConnected={metamaskConnected}
                    setMetamaskConnnected={setMetamaskConnnected}
                    account={account}
                // handleConnectWallet={handleConnectWallet}
                />
                <Switch>
                    <Route exact path="/">
                        <Home />
                    </Route>
                    {/* <Route exact path="/roadmap">
                        
                            <div
                                style={{
                                    marginTop: '10em',
                                }}>
                                <PDFReader
                                    url="https://nakedheadz.com/roadmap.pdf"
                                    showAllPage={true}
                                />
                            </div>
                        </Route> */}
                    {/* <Route exact path="/utility">
                            <Utility />
                        </Route> */}
                    <Route exact path="/whitepaper">
                        <div
                            style={{
                                marginTop: '10px',
                            }}>
                            <PDFReader
                                url="https://nakedheadz.com/whitepaper.pdf"
                                showAllPage={true}
                            />
                        </div>
                    </Route>
                    <Route exact path="/raffle">
                        <Raffle
                            metamaskConnected={metamaskConnected}
                            account={account}
                            setMetamaskConnnected={setMetamaskConnnected}
                        />
                    </Route>
                    {/* <Route exact path="/mint">
                            <Mint 
                                metamaskConnected={metamaskConnected}
                                account={account}
                                setMetamaskConnnected={setMetamaskConnnected}
                            />
                        </Route> */}
                </Switch>
            </Router>
        </Provider>
    );
}
