import React, { useEffect, useRef, useState } from 'react';
import Footer from './components/footer/Footer';
import Slide from './components/slide/Slide';
import { gsap } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import anime from 'animejs';
import ImgAnim1 from './img/banner/Gecko_website-p-800.jpeg';
import ImgAnim2 from './img/banner/Gianni_website-p-800.jpeg';
import ImgAnim3 from './img/banner/Hitman_Website-p-800.jpeg';
import ImgAnim4 from './img/banner/Ripley-p-800.jpeg';
import ImgAnim5 from './img/banner/Nurse--p-800.jpeg';

function Utility() {
    const imgOne = useRef();
    const imgTwo = useRef();
    const imgThree = useRef();
    const imgFour = useRef();
    gsap.registerPlugin(ScrollTrigger);
    useEffect(() => {
        // uility images aniamtion
        const tl = gsap.timeline({
            repeat: 1000,
            delay: 'none',
        });
        tl.to('#img-anim-box', {
            duration: 2,
            y: -1000,
            stagger: {
                each: 0.3,
                from: 'end',
            },
        });

        // utility text content animation
        gsap.from('#more-text', {
            translateY: '100%',
            stagger: 1,
            scrollTrigger: {
                trigger: '#than-text',
                scrub: 1,
                end: '100% 100%',
            },
        });

        gsap.from('#than-text', {
            translateY: '100%',
            stagger: 1,
            scrollTrigger: {
                trigger: '#than-text',
                scrub: 1,
                end: '100% 100%',
            },
        });
        gsap.from('#just-text', {
            translateY: '100%',
            stagger: 1,
            scrollTrigger: {
                trigger: '#than-text',
                scrub: 1,
                end: '100% 100%',
            },
        });
        gsap.from('#jpg-text', {
            translateY: '100%',
            stagger: 1,
            scrollTrigger: {
                trigger: '#than-text',
                scrub: 1,
                end: '100% 100%',
            },
        });
    });
    return (
        <div>
            <div className="utility-box">
                <section className="img-amin-box">
                    <img className="img-box-5" src={ImgAnim5} alt="" />
                    <img
                        ref={imgFour}
                        className="img-box-4"
                        id="img-anim-box"
                        src={ImgAnim4}
                        alt=""
                    />
                    <img
                        ref={imgThree}
                        className="img-box-3"
                        id="img-anim-box"
                        src={ImgAnim3}
                        alt=""
                    />
                    <img
                        ref={imgTwo}
                        className="img-box-2"
                        id="img-anim-box"
                        src={ImgAnim2}
                    />
                    <img
                        ref={imgOne}
                        className="img-box-1"
                        id="img-anim-box"
                        src={ImgAnim1}
                        alt=""
                    />
                </section>
            </div>

            <div>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8">
                            <div className="utility-info">
                                <h2>
                                    <div
                                        id="more-text-box"
                                        style={{ overflow: 'hidden' }}>
                                        <span
                                            style={{ display: 'inline-block' }}
                                            id="more-text">
                                            MORE
                                        </span>
                                    </div>
                                    <div style={{ overflow: 'hidden' }}>
                                        <span
                                            style={{ display: 'inline-block' }}
                                            id="than-text">
                                            THAN☺︎
                                        </span>
                                    </div>
                                </h2>
                                <h2>
                                    <div style={{ overflow: 'hidden' }}>
                                        <span
                                            style={{ display: 'inline-block' }}
                                            id="just-text">
                                            JUST A
                                        </span>
                                    </div>{' '}
                                    <div style={{ overflow: 'hidden' }}>
                                        <span
                                            style={{ display: 'inline-block' }}
                                            id="jpg-text">
                                            JPEG.
                                        </span>
                                    </div>
                                </h2>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-end mt-5">
                        <div className="col-lg-8">
                            <div className="row">
                                <div className="col-lg-6">
                                    <div className="ulitity-info-bottom">
                                        <h2>COMMUNITY + GIVEAWAYS</h2>
                                        <p>
                                            All our holders will get the chance
                                            to win regular prizes while
                                            contributing to the future progress
                                            of Naked Headz.
                                        </p>
                                        <p>
                                            Rare merch is being hoarded at NH HQ
                                            ready for distribution across next
                                            year.
                                        </p>
                                        <p>
                                            Owning a Head grants you access to
                                            i/O Underground benefits like
                                            airdrop tokens and limited edition
                                            NFTs. See our roadmap page for more
                                            info.
                                        </p>
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <div className="ulitity-info-bottom">
                                        <h2>PREMIUM PRIZES</h2>
                                        <p>
                                            Our first commitment to one lucky
                                            minter will be a brand new Tesla.
                                            This will be issued immediately
                                            after the mint.
                                        </p>
                                        <p>
                                            We are in the process of securing
                                            tickets to next years World Cup in
                                            Qatar, Coachella + Glastonbury 2022
                                            to giveaway, plus a shed load of
                                            merch and airdrop content from our
                                            artist partners.
                                        </p>
                                        <p>
                                            A list of prizes will be updated
                                            each month and raffled off to our
                                            community. We are developing a
                                            mechanic to ensure no one NFT can
                                            holder can win twice, giving fair
                                            distribution until everyone of our
                                            holders receives something from 'The
                                            Crypt Keeper'.
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-end mt-5 mb-5">
                        <div className="col-lg-9">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="utility-info-content">
                                        <h2>
                                            Royalties→ 2% on all future sales to
                                            the original minter
                                        </h2>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Slide />
            <Footer />
        </div>
    );
}

export default Utility;
