import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';

function Slide() {
    const textSlide = useRef();
    useEffect(() => {
        gsap.to(textSlide.current, {
            x: '-100%',
            duration: 20,
            ease: 'none',
            repeat: 100,
        });
    }, []);
    return (
        <>
            <section className="info-slider-text">
                <div
                    ref={textSlide}
                    className="d-flex align-content-center gap-5">
                    <h2>MINTING FEBRUARY 2022</h2>
                    <h2>☺︎</h2>
                    <h2>MINTING FEBRUARY 2022</h2>
                    <h2>☺︎</h2>
                    <h2>MINTING FEBRUARY 2022</h2>
                    <h2>☺︎</h2>
                    <h2>MINTING FEBRUARY 2022</h2>
                    <h2>☺︎</h2>
                    <h2>MINTING FEBRUARY 2022</h2>
                    <h2>☺︎</h2>
                    <h2>MINTING FEBRUARY 2022</h2>
                    <h2>☺︎</h2>
                </div>
            </section>
        </>
    );
}

export default Slide;
