import React, { useEffect, useRef } from 'react';
import { Link } from "react-router-dom";
import './Mint-header.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import GeckoImg1 from '../../img/banner/Gecko_website-p-800.jpeg';
import GeckoImg2 from '../../img/banner/Ripley-p-800.jpeg';
import GeckoImg3 from '../../img/banner/Gianni_website-p-800.jpeg';
import GeckoImg4 from '../../img/banner/Hitman_Website-p-800.jpeg';
import GeckoImg5 from '../../img/banner/Nurse--p-800.jpeg';
import GeckoImg6 from '../../img/banner/Terminator-p-800.jpeg';
import GeckoImg7 from '../../img/banner/Thekids-p-800.jpeg';
import NhLogo1 from '../../img/banner/NHLogo_v3.jpg';
import NhLogo2 from '../../img/banner/NHLogo_v2-p-800.jpeg';
import NhLogo3 from '../../img/banner/NHLogo_v1-p-800.jpeg';
import NhLogo4 from '../../img/banner/NHLogos_v4-p-800.jpeg';

function MintHeader() {
    const imgBox1 = useRef();
    const imgBox2 = useRef();
    const imgBox3 = useRef();
    const imgBox4 = useRef();
    const bannerHeadline = useRef();
    gsap.registerPlugin(ScrollTrigger);
    useEffect(() => {
        gsap.to(imgBox1.current, {
            duration: 20,
            y: '64%',
            repeat: 100,
            ease: 'none',
        });
        gsap.to(imgBox2.current, {
            duration: 20,
            y: '-100%',
            repeat: 100,
            ease: 'none',
        });
        gsap.to(imgBox3.current, {
            duration: 20,
            y: '64%',
            repeat: 100,
            ease: 'none',
        });
        gsap.to(imgBox4.current, {
            duration: 20,
            y: '-100%',
            repeat: 100,
            ease: 'none',
        });
        gsap.to(bannerHeadline.current, {
            scale: 0.8,
            duration: 10,
            ease: 'none',
            scrollTrigger: {
                trigger: bannerHeadline.current,
                start: 'bottom center',
                end: 'top 100px',
                scrub: 1,
            },
        });
    }, []);
    return (
        <>
            <section className="minter-box">
                <div className="d-flex align-items-center vh-100">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-6">
                                <div className="minter-content text-center">
                                    <h6>
                                        10K Generative NFT Avatars
                                    </h6>
                                    <h2 ref={bannerHeadline}>WELCOME TO NAKEDHEADZ MINT PAGE</h2>
                                    {/* <Link to="/raffle">Enter Whitelist Raffle</Link> */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div
                    ref={imgBox1}
                    className="minter-slide-img-01 position-fixed">
                    <img src={GeckoImg3} alt="img3" />
                    <img src={GeckoImg2} alt="img2" />
                    <img src={GeckoImg1} alt="img1" />
                    <img src={GeckoImg4} alt="img4" />
                </div>
                <div
                    ref={imgBox2}
                    className="minter-slide-img-02 position-fixed">
                    <img src={NhLogo2} alt="img2" />
                    <img src={NhLogo1} alt="img1" />
                    <img src={NhLogo4} alt="img4" />
                </div>
                <div
                    ref={imgBox3}
                    className="minter-slider-img-03 position-fixed">
                    <img src={GeckoImg1} alt="img1" />
                    <img src={GeckoImg6} alt="img6" />
                    <img src={GeckoImg5} alt="img5" />
                    <img src={GeckoImg7} alt="img7" />
                </div>
              
                <div
                    ref={imgBox4}
                    className="minter-slide-img-04 position-absolute">
                    <img src={NhLogo4} alt="img4" />
                    <img src={NhLogo3} alt="img3" />
                </div>
            </section>

                       
        </>
    );
}

export default MintHeader;
